/* src/components/header.css */

.header {
    background-color: #2c3e50;
    padding: 15px;
    box-shadow: 0 2px 5px rgba(0, 0, 0, 0.1);
  }
  
  .nav {
    display: flex;
    justify-content: space-between;
    align-items: center;
  }
  
  .nav-links {
    list-style-type: none;
    display: flex;
    margin: 0;
    padding: 0;
  }
  
  .nav-link {
    text-decoration: none;
    color: white;
    font-size: 16px;
    margin-right: 20px;
    padding: 10px;
    transition: all 0.3s ease-in-out;
    background: linear-gradient(135deg, #34495e, #2c3e50);
    border-radius: 5px;
  }
  
  .nav-link:hover {
    color: #ecf0f1;
    background-color: #3498db;
  }
  
  .nav-btn {
    padding: 8px 16px;
    background-color: #e74c3c;
    color: white;
    border: none;
    border-radius: 5px;
    cursor: pointer;
    font-size: 14px;
    transition: background-color 0.3s;
  }
  
  .nav-btn:hover {
    background-color: #c0392b;
  }
  
  .user-info {
    color: white;
    font-weight: bold;
  }
  
  @media (max-width: 768px) {
    .nav-links {
      flex-direction: column;
      align-items: flex-start;
    }
  
    .nav-link {
      margin-right: 0;
      margin-bottom: 10px;
    }
  
    .nav-btn {
      margin-top: 10px;
    }
  }
  