body {
  font-family: Arial, sans-serif;
  margin: 0;
  padding: 1rem;
  background: #f9f9f9;
}

h1 {
  color: #333;
}

input {
  padding: 0.5rem;
  margin: 0.5rem 0;
}

button {
  padding: 0.5rem 1rem;
  background: #007bff;
  color: white;
  border: none;
  cursor: pointer;
}

ul {
  list-style-type: none;
  padding: 0;
}

li {
  display: inline;
  margin-right: 1rem;
}
