/* src/components/links.css */

.links-list {
    list-style-type: none;
    padding: 0;
    margin: 0;
  }
  
  .link-item {
    font-size: 18px;
    text-decoration: none;
    color: #34495e;
    padding: 8px 16px;
    margin: 10px 0;
    display: block;
    transition: all 0.3s ease;
    border: 1px solid #ddd; /* Border added */
    border-radius: 5px; /* Rounded corners */
    background: linear-gradient(135deg, #ecf0f1, #bdc3c7); /* Gradient background */
  }
  
  .link-item:hover {
    background-color: #ecf0f1;
    color: #2c3e50;
    box-shadow: 0 4px 6px rgba(0, 0, 0, 0.1); /* Subtle shadow on hover */
    border-color: #3498db; /* Change border color on hover */
  }
  
  @media (max-width: 768px) {
    .link-item {
      padding: 10px 20px;
    }
  }
  